






















































import header from "../../../components/header.vue"
export default { 
	components:{
		headers:header
	},
	data(){
		return {
			imgSrc:"../../../static/img/back.png",
			title:"注册",
			user:"",
			pass:"",
			newpass:"",
			confirmpass:""
			// imgSrc:imgs
		}
	},
	computed:{
		isDisabled(){
			if(this.user.length == 11){
				if(this.vali.is_phone(this.user)){
					return true;
				}else{
					return false;
				}
				
			}else{
				return false;
			}
		}
	},
	methods:{
		back(){
			uni.navigateBack();
		},
		goInfo(){
			uni.navigateTo({
			    url: '/pages/page/info/info'
			});
		},
		sendyzm(){
			if(!this.vali.is_phone(this.user)){
				uni.showToast({
					icon:"none",
					title: '手机号码格式不正确且只能输入手机号',
					duration: 2000
				});
				this.user = "";
				return false;
			}
			this.yzmTo();
		},
		yzmTo(){
			
			uni.request({
				url: this.userUrl + "/BtCApi/Login/LoginSms", 
				method:'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				data: {
					Mob: this.user,
					type: 1,
					DeviceId: "",
					PhoneVersion: "",
					ClientVersion: "",
					ClientType: 0,
				},
				success: (res) => {
					console.log(res.data);
					uni.showToast({
						icon:"none",
						title: res.data.msg,
						duration: 2000
					});
				}
			});
		},
		isLogin(){
				
			if(this.pass.length == 0){
				uni.showToast({
					icon:"none",
					title: "验证码不能为空",
					duration: 2000
				});
				return false;
			}
			if(this.newpass.length == 0 ){
				uni.showToast({
					icon:"none",
					title: "",
					duration: 2000
				});
				return false;
			}
			if(this.confirmpass != this.newpass){
				uni.showToast({
					icon:"none",
					title: "两次密码输入不一致",
					duration: 2000
				});
				return false;
			}
			this.login();
		},
		login(){
			const _this = this;
			uni.request({
				url: this.userUrl + "/BtCApi/Login/APP_LoginRegister", //仅为示例，并非真实接口地址。
				method:'POST',
				 header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				data: {
					Verification_Code: this.pass,
					Mob: this.user,
					LoginPass:this.newpass,
					OkPass:this.confirmpass,
					DeviceId: '',
					PhoneVersion: null,
					ClientVersion: '',
					ClientType: 0,
				},
				success: (res) => {
					console.log(res.data);
					if(res.data.status == true && res.data.code == 30000){
						
						uni.switchTab({
							url: '/pages/page/login/login'
						});
					}else{
						uni.showToast({
							icon:"none",
							title: res.data.msg,
							duration: 2000
						});
					}
					
				}
			});
		}
	}
}
